@import "mixins";


.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;
    height: 104px;
    background-color: #fff;
    font-family: 'lucida-sans-unicode-regular', sans-serif;
    border-bottom: 3px solid #625C5C;
    z-index: 3;
    &::before {
        position: absolute;
        content: "";
        background-color: $colorBlue;
         right: 0;
         top: 0;
         width: 70%;
         height: 100%;
         @media (max-width: 1700px) {
             width: 80%;
         }
         @include tablet {
             width: 70%;
         }
         @include mobile2 {
            width: 100%;
        }
    }
    @include tablet2 {
       height: 70px;
    }
    // .header__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
    }

    // .header__logo

    &__logo {
        position: relative;
        z-index: 3;
        
    }

    // .header__content

    &__content {
        height: 100%;
         @include flex-all-sb;
    }

    // .header__menu
    &__menu.active {
        left: 0;
    }
    &__menu {
        width: 770px;
        height: 100%;
        position: relative;
        @include flex-all-center;
        ul {
            @include flex-all-sb;
            width: 100%;
            max-width: 650px;
            z-index: 2;
            position: relative;
            @include mobile {
                flex-direction: column;
            }
            li {
                @include mobile {
                   margin: 0 0 20px 0;
                }
            }
        }

        @media (max-width: $totalContainer) {
            position: fixed;
            width: 100%;
            height: 100%;
            top: 0;
            left: 100%;
            background-color: $colorRed;
            z-index: 1;
            transition: .3s all ease 0s;
            overflow-y: auto;
        }
        
    }

    // .header__item

    &__item {
        font-size: 20px;
        color: #fff;
        font-size: 20px;
        line-height: 24px;
        transition: .2s all ease 0s;
        font-weight: 400;
        @include mobile {
            font-size: 25px;
        }
    }

    // .header__menubg

    &__menubg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        @media (max-width: $totalContainer) {
           display: none;
        }
    }

    // .header__number

    &__number {
        position: relative;
        width: 288px;
        height: 100%;
        z-index: 3;
        @include tablet {
           width: auto;
           margin: 0 20px 0 0;
        }
    }

    // .header__burger
    &__burger.active {
        &::after {
            transform: rotate(-45deg);
            top: 9px;
            @include mobile {
                top: 10px;
            }
        }
        &::before {
            transform: rotate(45deg);
            bottom: 9px;
            @include mobile {
                bottom: 10px;
            }
        }
        span {
            display: none;
        }
    }
	&__burger {
        display: none;
        position: relative;
        width: 32px;
        height: 19px;
        z-index: 3;
        cursor: pointer;
        @include mobile {
            width: 27px;
            height: 22px;
        }
        @media (max-width: $totalContainer) {
            display: block;
        }
        &::after,
        &::before {
            position: absolute;
            content: "";
            display: block;
            background-color: #fff;
            width: 100%;
            height: 2px;
            left: 0;
            transition: .2s all 0s linear;
        }
        &::after {
            top: 0;
        }
        &::before {
            bottom: 0;
        }
        span {
            position: absolute;
            display: block;
            width: 100%;
            height: 2px;
            left: 0;
            top: 8px;
            background-color: #fff;
            @include mobile {
                top: 10px;
            }
        }
	}

}


.phone-number {
    @include flex-all-center;
    // .header-number__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        
        @include tablet {
            display: none;
        }
    }

    // .header-number__content

    &__content {
        position: relative;
        z-index: 2;
       display: flex;
       align-items: center;
    }

    // .header-number__ico

    &__ico {
        width: 25px;
        height: 25px;
        margin: 0 8px 0 0;
        @media (max-width: 350px) {
            display: none;
        }
    }

    // .header-number__text

    &__text {
        
        text-transform: uppercase;
        color: #fff;
        font-size: 25px;
        font-weight: 700;
        @include mobile2 {
            font-size: 20px;
        }
    }
}

